import Vue from 'vue'
import Router from 'vue-router'

import goodslist from '@/page/goods-list';
import pay from '@/page/pay';
import page404 from '@/page/page404';

// const goodslist = () =>
//   import ('@/page/goods-list');
// const pay = () =>
//   import ('@/page/pay');
// const page404 = () =>
//   import ('@/page/page404');

Vue.use(Router)
export default new Router({
  mode: "history",
  base: "/index/form/",
  routes: [{
      path: '/:key1?/:edid?/:key2?/:no?/goodslist',
      name: 'goodslist',
      component: goodslist,
      beforeEnter: function (to, from, next) {


        if (to.params.no) { //如果有no则是通过扫单个商品id进来的 那么重定向到pay页面

          const no = to.params.no;
          const edid = to.params.edid;

          next({
            path: "/pay/" + edid + "/" + no,
          });

        } else {

          next();

        }

      }
    },
    {
      path: '/pay/:edid?/:no?',
      name: 'pay',
      component: pay,
      beforeEnter: function (to, from, next) {

        const hostName = Vue.prototype.$base.parseURL(document.referrer).host;

        // Vue.prototype.$config.hostName 为合法域名 在li/config.js下配置
        if (hostName == Vue.prototype.$config.hostName) { //pay页面只能由合法域名进入 其他手段进入非法

          next();

        } else {

          next({
            name: "page404"
          })
        }

      }

    },
    // {
    //   path: "/:a?/:ad?/static/:key1?/:key2?/:key3?/:key4?/:key5?",
    //   beforeEnter: function (to, from, next) {
    //     const key1 = to.params.key1 || "";
    //     const key2 = to.params.key2 || "";
    //     const key3 = to.params.key3 || "";
    //     next("/static/wap/static/" + key1 + key2 + key3);
    //   }
    // },
    {
      path: "*",
      name: "page404",
      component: page404
    }

  ]
})



