<template>
    <div id="detail" class="fullX" @touchmove="touchmove">
        <div class="major">
            <div class="title flexC justSb">
                <img class="logoImg fullY" :src="detail.coverimg" alt="">
                <div class="right fullY grow1 column flexT justSa ">
                    <span>名称 : {{detail.title}}</span>
                    <span>价格 : <label style="color:#fe1c41">{{detail.price}}</label>  元</span>
                </div>
                
            </div>

            <div class="figure fullX" v-html="detail.content">
                <!-- {{detail.content}} -->
            </div>
        </div>
        <div class="footer fullX flex">
            <div class="left fullY flex50 shrink0 grow0 flexC justC c_f" @click="check">
                 <div class="checkBox p_r"><img class="icon p_a pos_C" v-show="checked[idx]==true"  src="../assets/img/yes.png" alt=""></div><span>{{checked[idx]==true ? "已选择" : "选择"}}</span>
            </div>
             <div class="right fullY flex50 shrink0 grow0 flexC justC c_f" @click="decide">
                 继续购买
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data: function() {
    return {
      src: ""
    };
  },
  created:function(){

  },
  computed: {
    detail: function() {
      return this.$store.state.goodsList.detail;
    },
    checked:function(){
      return this.$store.getters["goodsList/check"];
    },
    idx:function(){
      return this.$store.state.goodsList.checked.idx
    }
  },
  created: function() {
    this.src = this.$config.src;
    
  },
  methods: {
    check: function() {
      this.$store.commit("goodsList/changeChecked");
    },
    decide: function() {
      
      this.$emit("decide", this.checked[this.idx]);
    },
    touchmove:function(e){
      //  e.preventDefault(); 
      if(e.stopPropagation) { 
         e.stopPropagation();  
      }else{
          e.cancelBubble = true; 
      }
    }
  }
};
</script>

<style lang="scss" scoped>
#detail {
  height: 85%;
  .major {
    height: 90%;
    padding: 0.135rem;
    overflow: scroll;
    box-sizing: border-box;
    .title {
      height: 0.92rem;
      color: #222;
      font-size: 0.146rem;
      .logoImg {
        width: 0.92rem;
      }
      .right {
        padding-left: 0.1445rem;
      }
    }
    .figure {
      img {
        max-width: 100%;
        font-size: 0.14rem;
        color: #222;
        line-height: 0.25rem;
      }
    }
  }
  .footer {
    height: 10%;
    font-size: 0.146rem;
    .left {
      background: #fe661c;
      .checkBox{
        border: 1px solid #fff;
        border-radius: 0.05rem;
        width: .15rem;
        height: .15rem;
        margin-right: .05rem;
        .icon{
          top:-2%;
        }
      }
    }
    .right {
      background: #fe1c41;
    }
  }
}
</style>



