export const init = {
  namespaced: true,
  state: {
    init: true, //是否第一次进goodslist页面来判断是否显示加载动画  //true显示 否则不显示
    system: {
      wh: null, //浏览器高度
    },
  },
  mutations: {
    changeInit: function (state, payload = false) {
      state.init = payload;
    },
    setHeight: function (state, wh) {
      state.system.wh = wh;
    }
  },
  actions: {

  },
  getters: {}
}



