<template>
    <div id="loading" class="full p_a t0 l0 zIdx1000 flexC justC column" v-show="show">
        <img class="title" src="../assets/img/loading_txt.png" alt="">
        <img class="logo" src="../assets/img/loading_pic.png" alt="">
        <div class="progress">
            <div class="inner fullY p_r" ref="scrollBar" :style="`width:${width}%`">
                <div class="bilibili p_a flexE justC c_f" style="height:.35rem;bottom:120%;right:5%">
                    <img style="margin-right:0.05rem"  src="../assets/img/bilibili.png" alt=""/>
                    <span >{{width}}%</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data: function() {
    return {
      width: 0,
      show: true,
      src: ""
    };
  },
  props: {
    over: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    over: function(n, o) {
      if (n) {
        this.beginScroll(100, () => {
          this.endScroll();
        });
      }
    },
    show: function(n) {
      if (!n) {
        this.$emit("loadingHidden", true);
      }
    }
  },
  created: function() {
    this.src = this.$config.src;
    const num = 75 + Math.random() * 25;
    this.beginScroll(num);
  },
  methods: {
    beginScroll: function(num = 100, fn) {
      var timer = setInterval(() => {
        if (this.width < num) {
          this.width += 1;
        } else {
          clearInterval(timer);
          timer = null;
          if (typeof fn == "function") {
            fn();
          }
        }
      }, 6);
    },
    endScroll: function() {
      setTimeout(() => {
        this.show = false;
      }, 300);
    }
  }
};
</script>

<style scoped lang="scss">
@import "../assets/css/common.scss";
#loading {
  background: #ff3e5e;
  z-index: 10000000000000000000;
  .title {
    width: 50%;
    margin-bottom: 0.2rem;
  }
  .logo {
    width: 50%;

    height: 54vw;
    height: 54vmax;
    height: 54vmin;
  }
  .progress {
    width: 3.375rem;
    height: 0.15rem;
    border-radius: 0.075rem;
    background: #700012;
    margin-top: 0.6rem;
    .inner {
      border-radius: 0.075rem;

      @include linear(#f02d9f,#ff9c8f,left);
      box-shadow: 1px -2px 0px 0.5px #f5b3d1;
    }
  }
}
</style>




