<template>
  <div class="v_button flexC justC p_f b0 bd_f" >
      <div class="btn flexC justC">
          {{mes}}<slot></slot>
      </div>
  </div>
</template>

<script>
export default {
  props: {
   
    mes: {
      type: String,
      default: "确定"
    }
  }
};
</script>

<style lang="scss" scoped>
.v_button {

  height: 0.42rem;
  width: 100%;
  padding: 0.09rem 0;
 
  .btn {
    width: 2.92rem;
    height: 100%;
    background: #fe1c41;
    color: #fff;
    font-size: 0.16rem;
    border-radius: 0.21rem;
  }
}
</style>





