<template>
  <div id="pay"  class="container full">
    <v-header :back="true" mes="确认选择"></v-header>
    <div class="list">
        <div class="items flexC" v-for="(val,idx) in list" :key="idx">
      
            <img  :src="val.coverimg" alt=""/>
            <div class="detail fullY flex justSa column">
                <span>编号:{{val.id}}</span>
                <span>品名:{{val.title}}</span>
                <span>单价￥<span class="c_r">{{val.price}}</span>元</span>
            </div>
        </div>
    </div>
    <div class="payInfo padX flexC bd_f">
        共需支付：￥ <span class="c_r">{{totalPrice}}</span>元
        <!-- isWeiXin{{isWeiXin}} -->
    </div>   
    <v-button style="background:#f4f4f4" mes="立即支付" @click.native="subMit"></v-button>
    <!-- 支付功能由一个隐藏的表单完成 -->
    <form v-if="!isWeiXin" style="display:none;height:0;width:0" action="/api/alipay/alipay.html" method="post" name="alipayment">
        <input type="text" name="ordersn"  v-model="aliPayMes0.ordersn">
        <input type="text" name="orderprice"  v-model="aliPayMes0.orderprice">
         <input type="text" name="edid"  v-model="aliPayMes0.edid">
        <button type="submit" ref="subbtn"></button>
    </form>
    <form v-else style="display:none;height:0;width:0" action="/api/wxpay/index.html" method="post" name="wxpayment">
        <input type="text" name="ordersn"  v-model="wxpayMes0.ordersn">
        <input type="text" name="orderprice"  v-model="wxpayMes0.orderprice">
        <input type="text" name="edid"  v-model="wxpayMes0.edid">
        <button type="submit" ref="wxPayBtn"></button>
    </form>
  </div>
</template>
<script>
var SUBFLAG = true; //防止多次点击
export default {
  data: function() {
    return {
      list: [],
      totalPrice: 0,
      aliPayMes0: {
        orderprice: "",
        ordersn: ""
      },
      wxpayMes0: {
        orderprice: "",
        ordersn: "",
        edid: ""
      },
      isWeiXin: false //浏览器环境 微信浏览器和非微信浏览器
    };
  },
  computed: {},
  created: function() {
    this.isWeiXin = this.$base.isWeiXin();
  
    if (this.$route.params.no && this.$route.params.edid) {
      //扫单个商品进来的
      const no = this.$route.params.no;
      const edid = this.$route.params.edid;
      this.reqInfo(edid, no);
    } else {
      //不是扫单个商品进来的
      // const hostName = this.$base.parseURL(document.referrer).host;
      // if (hostName != this.$config.returnUrl) {//暂时不做判断 因为支付完成都回去首页
      //和支付没关系 正常渲染
      this.normal();
      // } else {
      //   this.aliPayResult();
      // }
    }
  },
  methods: {
    reqInfo: function(edid, no) {
      this.$http
        .post("/api/form/gethd_info", {
          edid,
          no
        })
        .then(res => {
          console.log(res);
          if (res.data.code == 200) {
            let arr = [];
            arr.push(res.data.data);
            this.list = arr;
            this.totalPrice = res.data.data.price;
          } else {
            this.$mint.Toast({
              message: res.data.msg
            });
            if(res.data.code==204){
              // console.log(this.$route)
              setTimeout(() => {
                
                this.$router.replace(`/edid/${this.$route.params.edid}/goodslist`)
              }, 2500);
            }
          }
        });
    },
    normal: function() {


      let list, checkList;
      
      if (this.$store.state.goodsList.list[0]) {
        //如果页面f5刷新那么vuex的状态数据有可能就消失了那么就要从缓存里拿取，不过现在看来没有什么必要 先留着
        list = this.$store.state.goodsList.list;
        checkList = this.$store.getters["goodsList/check"];
        window.sessionStorage.setItem("list", JSON.stringify(list));
        window.sessionStorage.setItem("checkList", JSON.stringify(checkList));
      } else {
        //一般会走这里

        list = JSON.parse(window.sessionStorage.getItem("list"));
        checkList = JSON.parse(window.sessionStorage.getItem("checkList"));
      }

      let arr = [];
      let num = 0;
      checkList.map((val, idx) => {
        if (val) {
          num += Number(list[idx].price);
          arr.push(list[idx]);
        }
      });
      this.totalPrice = num;
      this.list = arr;
    
    },
    subMit: function() {
      if (!window.localStorage.getItem("edid")) {
        this.$mint.Toast({
          message: "信息失效，请重新登录"
        });
        return;
      }

      let edid = window.localStorage.getItem("edid");

      let arr = []; //商品列表信息
      this.list.map(val => {
        arr.push({ id: val.id, no: val.no });
      });

      if (!SUBFLAG) {
        return;
      } else {
        SUBFLAG = false;
      }
      this.$http.post("/api/index/order", { edid, list: arr }).then((res) => {
       
        
        if (res.data.code !== 200) {
          this.$mint.Toast({
            message: res.data.msg
          });
          SUBFLAG = true;
          return;
        }

        if (this.isWeiXin) {
          //微信环境 微信支付
          this.wxPay(res);
        } else {
          //普通浏览器环境用支付宝
          this.aliPay(res);
        }
      }).catch(err=>{
        SUBFLAG = true;
      });
      // return;
      // this.$router.replace("/goodsList");
    },
    wxPay: function(res) {
      this.wxpayMes0 = {
        orderprice: res.data.data.orderprice,
        ordersn: res.data.data.ordersn,
        edid: window.localStorage.getItem("edid")
      };

      this.$nextTick(() => {
        let btn = this.$refs.wxPayBtn;
        SUBFLAG = true;
        btn.click();
      });
    },
    aliPay: function(res) {
      //拉起支付宝支付
      this.aliPayMes0 = {
        orderprice: res.data.data.orderprice,
        ordersn: res.data.data.ordersn,
        edid: window.localStorage.getItem("edid")
      };
      let btn = this.$refs.subbtn;
      this.$nextTick(() => {
        SUBFLAG = true;
        btn.click();
      });
    },

    aliPayResult: function() {
      //暂时无用 都回首页 //现在在首页判断了
      //支付宝支付回调
      const query = this.$router.currentRoute.query;
      if (query.trade_no) {
        //获取订单了

        this.$mint.Toast({
          message: "付款成功，正在出货"
        });
        setTimeout(() => {
          this.$store.commit("init/changeInit");
          const edid = window.localStorage.getItem("edid");
          this.$router.replace(`edid/${edid}/goodsList`);
        }, 3000);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
#pay {
  background: #f4f4f4;
  .c_r {
    color: #fe1c41 !important;
  }
  .list {
    padding: 0.06rem;
    font-size: 0.155rem;
    .items {
      padding: 0.065rem 0.14rem 0.11rem 0.14rem;
      margin-bottom: 0.06rem;
      background: #fff;
      height: calc(2.13rem/2);
      img {
        width: calc(1.58rem/2);
        height:calc(1.58rem/2);

      }
      .detail {
        margin-left: 0.15rem;
      }
    }
  }
  .payInfo {
    height: calc(1.11rem/2);
    font-size: 0.155rem;
    color: #222222;
  }
}
</style>



