export const goodsList = { //商品列表
  namespaced: true,
  state: {
    detail: { //所点击查看的商品详情
    },

    list: [], //列表
    checked: { //点击的商品
      idx: null, //激活的商品list角标
    }
  },
  getters: {
    check: state => { //每个list商品对应选中状态；
      var arr = [];
      state.list.map(val => {
        arr.push(val.checked)
      })
      return arr;
    }
  },
  mutations: {
    changeDetail(state, payload) { //切换详情商品列表
      state.detail = payload.data;
      state.checked.idx = payload.idx
    },
    changeIdx(state, payload) { //切换list的选中状态
      state.list[payload].checked = !state.list[payload].checked
    },
    changeList(state, payload) { //更改整个商品列表
      state.list = payload;

    },
    beginScale: function (state, payload) { //单个商品动画（缩放完毕）接受角标
      Vue.set(state.list[payload], "act", true)
    },
    changeChecked: function (state) { //切换对应商品选中状态
      state.list[state.checked.idx].checked = !state.list[state.checked.idx].checked
    },
    clearList: function (state) {
      state.list = []
    }
  },
  actions: {

  },

}



