import Vue from "vue";
import Vuex from "vuex";
import {init} from "./init.js";
import {goodsList} from "./goodsList.js";
Vue.use(Vuex);
//Vuex的总部 另外每个页面的数据中心都新建了一个响应的文件并且分块了
const store = new Vuex.Store({
  modules: {
    init,
    goodsList
  },
  state: {

  },
  mutations: {

  }
})
export default store



