<template>
<keep-alive>
 <div id="goodsList" ref="goodsList" class="container p_r " :class="{loading : !delayCoverHidden && init}">
      <emptyPage v-if="!list[0] && over"></emptyPage><!--空页面明天加-->

      <loading v-if="init" :over="over" @loadingHidden="loadingHidden"></loading>
        <div id="cover" @click="coverClick" @touchmove="touchmove" :class="{act:coverMes.show}" class="p_f t0 b0 full" >
          
        </div>
         <detail @decide="decide"  class="detail p_f b0 bd_f"  :class="{act:coverMes.show}"></detail>
        <v-header @click.native="scrollTop()" ref="header"></v-header> 
          <load-more  ref="loadmore"  :top-method="onRefresh">
           <div class="list flexT flexW overS">
            <div class="items shrink0 grow0 flexC justC column " v-if="val" :class="{act : val.act}" v-for="(val,idx) in list" :key="idx" >
                <div class="imgBox p_r">
                  <!-- <transition name="fade"> -->
                    <div class=" cover p_a  pos_C" :class="{active : val.checked}"  @click="choose(idx,val.id)">
                         <img class="icon p_a pos_C" src="../assets/img/checked.png" alt=""/>                       
                    </div> 
                    <div class="c_f textC p_a pos_C flexC justC column" v-show="val.stock<=0"  style="line-height:0.25rem;background:rgba(0,0,0,0.4)">
                            <div>已售罄</div>
                            <div style="font-size:12px;">看看其他商品吧</div>

                         </div>
                 
                  <lazy-component class="full" @show="handler(idx)">
                    <img v-if="val.coverimg"  @click="choose(idx,val.id)" class="pic full surfaceImg " v-lazy="`${val.coverimg}`" :src="val.coverimg"  alt="">
                    <img v-else class="pic full surfaceImg " src="../assets/img/error.jpg" alt="">
                  </lazy-component>
                </div>
                <div class="mes flexC justC column fullX"  @click="checked(idx,val.id)">
                  <span>￥{{val.price}}</span>
                  <span>{{val.title}}</span>
                </div>
            </div>
            
            </div>    
        </load-more>         
        <v-button @click.native="subMit" ref="footer"></v-button>
  </div>

</keep-alive>
 
</template>
<script>
import loading from "../components/loading.vue";
import detail from "../components/detail.vue";

import emptyPage from "../components/empty-page.vue";
export default {
  data: function() {
    return {
      over: false,
      delayCoverHidden: false, //遮罩层真正消失的时候这里就是true 
      showList: [],//已经被加载的单个商品角标  暂时无用
      coverMes: {//详情信息
        //
        show: false, //是否显示详情页
        mes: {
          //所点击的商品信息 非详情
          idx: null
        },
        info: {
          //商品详情
        }
      },
      pullConfig: {//配置下拉刷新
        pullDownHeight: 50
      }, 
      scrollH: "400px" //滚动视图高度这只是个初始值
    };
  },

  computed: {
    init: function() {
      return this.$store.state.init.init;
    },
    list: function() {
      return this.$store.state.goodsList.list;
    }
  },
  watch: {
    // showList: function() {}
  },
  components: {
    loading,
    detail,
    emptyPage
  },

  created: function() {
    // 判断是否是初次进来决定是否展示动画 与loadingHidden中的方法对应 加载玩动画则 init为false

    // 判断是否是初次进来决定是否展示动画 与loadingHidden中的方法对应 加载玩动画则 init为false

    const isWeiXin = this.$base.isWeiXin();

    if (!isWeiXin) {
      //如果不是微信环境那么就要判断用户是扫码进来的还是支付完成后支付宝回调回来的 因为支付宝回调回来会有一大堆参数
      //支付宝环境
      const query = this.$router.currentRoute.query;
      if (
        query.trade_no &&
        query.total_amount &&
        query.timestamp &&
        query.sign
      ) {
        //不太保险的判断方法 先这样吧
        //获取订单了

        this.$mint.Toast({
          message: "付款成功，正在出货",
          position: "top",
          duration: 2000
        });

        this.$store.commit("init/changeInit", false);
      } else {
        this.normal();
      }
    } else {
      //微信环境

      this.normal();
    }

    this.reqInfo();//渲染页面  基本每个页面的初次渲染我都会写成reqInfo()
  },

  mounted: function() {//做一下设备信息查询
    const wh = window.screen.height;
    const headerH = this.$refs.header.$el.offsetHeight;
    const footerH = this.$refs.footer.$el.offsetHeight;
    this.scrollH = wh - headerH - footerH;
    this.$store.commit("init/setHeight", wh);
  },
  methods: {
    normal: function() {
      //没有支付跳转 正常的判断本地缓存是否是第一次进来
      var init;

      const edid = window.localStorage.getItem("edid");
      if (edid === this.$route.params.edid) {
        init = false;
      } else {
        init = true;
      }
      this.$store.commit("init/changeInit", init);
    },
    touchmove: function(e) {
      //  e.preventDefault();
      if (e.stopPropagation) {
        e.stopPropagation();
      } else {
        e.cancelBubble = true;
      }
    },
    coverClick: function() {
      this.coverMes.show = false;
    },
    scrollTop: function() {
      this.$base.scrollTop();
    },
    onRefresh: function() {
      this.loading = true;
      this.$mint.Indicator.open({
        text: "loading",
        spinnerType: "fading-circle"
      });
      var that = this;
      return new Promise(function(resolve, reject) {
        that.loading = false;
        that.reqInfo(function() {
          that.$mint.Indicator.close();
          that.$refs.loadmore.onTopLoaded();
          resolve();
        });
      });
    },
    reqInfo: function(fn) {
      const edid = this.$route.params.edid;
      this.$mint.Indicator.open({
        text: "loading",
        spinnerType: "fading-circle"
      });

      this.$http
        .post("/api/index", {
          edid
        })
        .then(res => {//因为axios不符合一般插件规范 所以为了避免每个组件里都引一次 所以我放到了Vue.prototype.$http里
          if (res.data.code == 200) {
            // console.log(res.data);
            this.$mint.Indicator.close();
            res.data.list.map(val => {//需要对后台返回来的数据加工一下  每一个上面加一个checked属性来标识这个商品是否被选中了
              // if (this.init) {
              //   //只有第一次进来执行动画
              //   val.act = false;
              // } else {xuan
              //   val.act = true;
              // }
              val.checked = false;
            });
            this.$store.commit("goodsList/changeList", res.data.list);//把数据交给vuex状态共享中心
            this.over = true;//接口请求完成 隐藏初次动画

            if (typeof fn == "function") {//这个reqInfo还会被下拉刷新调用  回调处理
              fn();
            }
          } else {
            this.$mint.Toast({
              message: res.data.msg
            });
          }
        });
    },
    beginScale: function(idx = this.list.length) {
      //动画弹出效果 暂时无用
      let i = 0;
      let timer = setInterval(() => {
        if (i < idx) {
          this.$store.commit("goodsList/beginScale", i);
          i++;
        } else {
          clearInterval(timer);
        }
      }, 300);
    },
    decide: function(e) {//用户选择商品
      this.coverMes.show = false;
      if (this.coverMes.mes.idx) {
        if (e) {
          this.list[this.coverMes.mes.idx].checked = true;
        } else {
          this.list[this.coverMes.mes.idx].checked = false;
        }
      }
    },
    subMit: function() {//提交商品到pay页面
      let flag = false;
      const check = this.$store.getters["goodsList/check"];
      for (let i = 0; i < check.length; i++) {
        if (check[i]) {
          flag = true;
          break;
        }
      }

      if (flag) {
        this.$router.push("/pay");
      } else {
        this.$mint.Toast({
          message: "请先选择商品"
        });
      }
    },
    checked: function(e, id) {
      //查看详情
      if (this.list[e].stock <= 0) {
        return;
      }
      this.$mint.Indicator.open({
        text: "loading",
        spinnerType: "fading-circle"
      });
      this.$http
        .post("/api/index/info", {
          id
        })
        .then(res => {
          // console.log(res);
          if (res.data.code == 200) {
            this.$store.commit("goodsList/changeDetail", {
              data: res.data.data,
              idx: e
            });
            this.$mint.Indicator.close();
            this.coverMes.mes.idx = e;
            this.coverMes.show = true;
          } else {
            throw Error(res.data.msg);
          }
        });
    },
    choose: function(e, id) {
      //选中购买

      this.$store.commit("goodsList/changeIdx", e);
    },
    handler: function(idx) {
      //暂时无用

      this.showList.push(idx);
    },
    loadingHidden: function(e) {
      if (e) {
        this.$store.commit("init/changeInit", false);
        window.localStorage.setItem("edid", this.$route.params.edid);
        this.delayCoverHidden = true;
        // this.beginScale();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/animation.scss";
#goodsList {
  background: #fff;
  &.loading {
    height: 100%;
    overflow: hidden;
  }
  #cover {
    background: rgba(0, 0, 0, 0);
    z-index: -1;

    transition: all 0.3s 0.1s;

    &.act {
      z-index: 1000000;
      background: rgba(0, 0, 0, 0.4);

      opacity: 1;
    }
  }
  .detail {
    border-radius: 0.15rem 0.15rem 0 0;

    z-index: 10000000;
    transform: scale(0);
    transform-origin: 50%;
    transition: all 0.3s 0.1s;
    &.act {
      transform: scale(1);
    }
  }
  .list {
    padding: 0 2%;
    .items {
      width: 30%;
      flex-basis: 30%;
      padding-left: calc(10%/6);
      padding-right: calc(10%/6);
      overflow: hidden;
      padding-top: 0.09rem;

      // transform: scale(0);

      &.act {
        // animation: myfirst 0.3s;
        transform: scale(1);
      }
      .imgBox {
        height: 30vw;
        width: 100%;
        border-radius: 0.1rem;
        overflow: hidden;
        .cover {
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0);
          border-radius: 0.1rem;
          transition: all 0.2s;
          transform: scale(0);
          &.active {
            transform: scale(1);
            background: rgba(0, 0, 0, 0.3);
          }
        }
      }

      .mes {
        line-height: 0.22rem;
        margin-top: 0.05rem;
      }
    }
  }
}
</style>





