<template>
   <div class="header fullX flexC justC p_f t0" >
      <img v-if="back" @click="goBack" class="backImg p_a "  src="../assets/img/Arrow.png" alt="">
       <span>{{mes}}</span>
   </div>
</template>
<script>
export default {
  props: {
    mes: {
      default: "饮品选择"
    },
    back:{
      type:Boolean,
      default:false
    }
  },
  methods: {
    // scrollTop: function() {
    //   this.$base.scrollTop();
    // },
    goBack:function(){
     
      this.$router.go(-1)
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  z-index: 100;
  background: #fe1c41;
  color: #fff;
  font-size: 0.16rem;
  height: 0.39rem;
  .backImg{
    transform: rotate(180deg);
    left: 2.5%;
    opacity: 0.8;
  }
}
</style>



